<script>
import { onMounted, ref, reactive, toRefs, getCurrentInstance, watch } from "vue";
import addico from '../assets/sections/expand.svg'
import delico from '../assets/sections/contract.svg'
export default {
  name: "Home",
  setup() {
    const state = reactive({
      pageModel: {
        current: 1,
        size: 20,
      },
      connect: {
        isConnect: false,
        address: "",
        balance: "",
      },

      totalSupply: 8585,
      mintTotalSupply: 0,
      currentContract: null,
      mintPrice: 0,
      mintNumber: "",
      showModel:{
        tip1:false,
        tip2:false,
        tip3:false,
        tip4:false,
        tip5:false,
        tip6:false,
        tip7:false,
        tip8:false,
        tip9:false,
        tip10:false,
        tip11:false,
        tip12:false,
        tip13:false,
        tip14:false,
        tip15:false,
        tip16:false,
        tip17:false,
        tip118:false,
        tip119:false,
      },
      imgAdd:addico,
      imgDel:delico,
    });
    const { proxy } = getCurrentInstance();

    state.connect = proxy.$store.state.connect;

    // 
    const omitshow = function (value, start, end) {
      if (value != "" && value != null && typeof value != "undefined") {
        var str =
          value.substring(0, start) +
          "..." +
          value.substring(value.length - end);
        return str;
      }
    };
    const connectWallet = async function () {
      const connectResult = await proxy.$utils.CONNECT.linkWallet();
      state.connect.isConnect = connectResult.state;
      if (connectResult.state) {
        state.connect.address = connectResult.address;
        state.connect.balance = connectResult.balance;
        proxy.$store.commit("changeconnect", {
          isConnect: true,
          address: connectResult.address,
          balance: connectResult.balance,
        });
      } else {
        alert(connectResult.msg);
      }
    };
    const disconnect = function () {
      state.connect = {
        isConnect: false,
        address: "",
        balance: "",
      };
      proxy.$store.commit("changeconnect", {
        isConnect: false,
        address: "",
        balance: "",
      });
    };
    //
    const getTotalSupply = async function () {
      let currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      if (currentContract != null) {
        state.currentContract = currentContract;
        state.mintTotalSupply = await proxy.$utils.CONTRACT.totalSupply();
      }
    };
    const getMint = async function () {
      if (state.mintNumber == "" || state.mintNumber == 0) {
        alert("Please enter the correct casting quantity");
        return false;
      }
      if (state.mintNumber > 200) {
        alert("The maximum casting quantity is 200");
        return false;
      }
      if (state.currentContract == null) {
        state.currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      }
      if (state.currentContract != null) {
        let balance = await proxy.$utils.CONNECT.getbalance(
          state.connect.address
        );
        let amount = state.mintNumber * state.mintPrice;

        if (Number(balance) < amount) {
          alert("Insufficient wallet balance");
          console.log(balance, amount);
          return false;
        }
        proxy.$utils.CONTRACT.publicMint(
          state.mintNumber,
          state.mintPrice,
          function (res) {
            alert("Casting success");
            state.mintNumber = "";
            setTimeout(async function () {
              getTotalSupply();
            }, 1000);
          },
          function (err) {
            alert("Casting failed. Please recast later");
          }
        );
      }
    };
    let fapList = ref([
      {title: 'What are PLURTron NFTs', content: 'PLURTron NFTs are unique TRC-721 tokens created on Tron blockchian, The project was inspired by hip-hop culture, Peace Love Unity Respect, commonly shortened to PLURTron.'},
      {title: 'How many PLURTron NFTs', content: 'Max Total Supply: 8585'},
      {title: 'What is the mint cost of a PLURTron NFT', content: '0.00 TRX, all FREE MINT'},
      {title: 'Why Free Mint', content: 'We missed the best time to market at the beginning of the year. According to the current situation, the project is insufficient to protect the holders interests. Therefore, after careful evaluation by the team, we have decided to make this project FREE MINT for everyone.'}
    ]);
    let faqIndex = ref(null)
    const methods = {
      faqShowContent(e, index) {
        faqIndex.value = faqIndex.value === index ? null : index
      }
    };
    onMounted(() => {
      setTimeout(async function () {
        getTotalSupply();
      }, 1000);
    });
    return {
      ...toRefs(state), //  state  state 
      ...methods,
      connectWallet,
      disconnect,
      getTotalSupply,
      omitshow,
      getMint,
      fapList,
      faqIndex
    };
  },
};
</script>



